.nav {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  background-color: lightgray;
  border-bottom: 2px solid whitesmoke;
}
.nav ul {
  list-style: none;
  margin-right: 30px;
  height: calc(100% - 5rem);
  max-height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.nav__logo {
  flex-basis: content;
  flex-shrink: 0;
  font-size: 28px;
  border: 2px solid #07237e;
  border-radius: 10px;
  padding: 10px;
}
.nav__logo:hover {
  border-color: #0a32b8;
  background-color: whitesmoke;
}
.nav__logo:hover a {
  color: #0a32b8;
}
.nav__logo a {
  text-decoration: none;
  color: #07237e;
}
.nav__item a {
  border: none;
  background-color: #07237e;
  color: whitesmoke;
  padding: 10px 20px;
  width: 140px;
  display: block;
  text-decoration: none;
  text-align: center;
  border-radius: 10px;
}
.nav__item a:hover {
  background-color: #0a32b8;
}
@media screen and (max-height: 400px) {
  .nav {
    height: 5rem;
    width: 100%;
  }
  .nav ul {
    height: 100%;
    margin-top: 0;
    flex-direction: row;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
  }
  .nav__item {
    flex: 0 1 140px;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
  }
  .nav__item a {
    width: 100%;
    padding: 10px 0;
  }
  .post,
  .about,
  .signup,
  .login,
  .logout,
  .posts,
  .new-post {
    margin: 5rem 0 !important;
  }
  .logout {
    width: 100% !important;
  }
}
@media screen and (max-width: 600px) {
  .nav__logo {
    display: none;
  }
  .nav ul {
    margin: 0 10px;
    padding: 0;
  }
  .nav ul li:nth-child(2) {
    display: none;
  }
  .nav {
    height: 5rem;
    width: 100%;
  }
  .nav ul {
    height: 100%;
    margin-top: 0;
    flex-direction: row;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
  }
  .nav__item {
    flex: 0 1 140px;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
  }
  .nav__item a {
    width: 100%;
    padding: 10px 0;
  }
  .post,
  .about,
  .signup,
  .login,
  .logout,
  .posts,
  .new-post {
    margin: 5rem 0 !important;
  }
  .logout {
